import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import { LitElement, html, css, customElement, property } from "@umbraco-cms/backoffice/external/lit";

@customElement('enterspeed-dashboard')
export class enterspeed_dashboard extends UmbElementMixin(LitElement) {

    constructor() {
        super();
    }

    @property()
    title = 'Enterspeed.Source.UmbracoCms dashboard'

    render() {
        return html`
            <uui-box headline="${this.title}">
                dashboard content goes here
            </uui-box>
            <uui-table aria-label="Example table" aria-describedby="#some-element-id">
                <uui-table-row>
                    <uui-table-cell>Cell 1</uui-table-cell>
                    <uui-table-cell>Cell 2</uui-table-cell>
                </uui-table-row>
            </uui-table>
        `
    }

    static styles = css`
        :host {
            display: block;
            padding: 20px;
        }
    `
}


export default enterspeed_dashboard;

declare global {
    interface HtmlElementTagNameMap {
        'enterspeed_dashboard': enterspeed_dashboard
    }
}